<template>
    <div>
         <div class="container px-0 px-md-3">
  <header class="header">
    <div class="header-main-image bg-cover" style="background-image: url(https://images.unsplash.com/photo-1473256599800-b48c7c88cd7e?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=ae0d68ab0db4ee79bdce92722b31112b&auto=format&fit=crop&w=925&q=80)"></div>
    <h1 class="text-hide">六角甜點工坊</h1>
    <div class="row no-gutters justify-content-center rectangle-section">
      <div class="col-md-10">
        <div class="row no-gutters">
          <a href="#" class="col-4 rectangle-image-wrap active">
            <span class="rectangle-text">本日精選</span>
            <div class="four-sides rectangle-over zindex--1"></div>
            <div class="rectangle-image bg-cover" style="background-image: url(https://images.unsplash.com/photo-1504473089979-b1c4993a9653?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=9db69b72e1f7d1b4e5c81cee531108ff&auto=format&fit=crop&w=634&q=80)">
            </div>
          </a>
          <a href="#" class="col-4 rectangle-image-wrap">
            <span class="rectangle-text">人氣推薦</span>
            <div class="four-sides rectangle-over zindex--1"></div>
            <div class="rectangle-image bg-cover" style="background-image: url(https://images.unsplash.com/photo-1525059337994-6f2a1311b4d4?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=7ce5bbf66bdb4ed3b6965f0b3618aa43&auto=format&fit=crop&w=654&q=80)">
            </div>
          </a>
          <a href="#" class="col-4 rectangle-image-wrap">
            <span class="rectangle-text">新品上市</span>
            <div class="four-sides rectangle-over zindex--1"></div>
            <div class="rectangle-image bg-cover" style="background-image: url(https://images.unsplash.com/photo-1490914327627-9fe8d52f4d90?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=73ed228f25bdefd2291c242f6d390fca&auto=format&fit=crop&w=634&q=80)">
            </div>
          </a>
        </div>
      </div>
    </div>
  </header>
</div>

<section class="section my-md-9">
  <div class="row justify-content-center no-gutters d-md-none">
    <div class="col-6">
      <img src="/images/sm-為什麼選擇了做甜點.svg" class="my-5 img-fluid" alt="">
    </div>
  </div>
  
  <div class="bg-primary-lighter">
    <div class="container">
      <div class="row">
        <div class="col-md-6 break-vertical-block">
          <div class="bg-cover h-100 section-intro-image" style="background-image: url(https://images.unsplash.com/photo-1519730722595-a5ff788dea4d?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=2712a33fe9b13532f0682ac4e1e749b8&auto=format&fit=crop&w=634&q=80)"></div>
        </div>
        <div class="col-md-6 vertical-content d-flex justify-content-end">
          <div class="writing-md-vertical py-md-7 py-5 text-primary-light mr-auto">
            <p class="mb-0">青山依舊在，幾度夕陽紅。慣看秋月春風。</p>
            <p class="mb-0">一壺濁酒喜相逢，浪花淘盡英雄。是非成敗轉頭空，滾滾長江東逝水，白髮漁樵江渚上，古今多少事，都付笑談中。</p>
            <p class="mb-0">是非成敗轉頭空，滾滾長江東逝水，白髮漁樵江渚上，古今多少事，都付笑談中。</p>
          </div>
          <div class="break-vertical-block d-none d-md-block">
            <img src="/images/lg-為什麼選擇了做甜點.svg" width="88" alt="">
          </div>
        </div>
      </div>
    </div>
  </div>
</section>

<section class="section my-md-9">
  <div class="row justify-content-center no-gutters d-md-none">
    <div class="col-6">
      <img src="/images/sm-為什麼一定要吃甜點.svg" class="my-5 img-fluid" alt="">
    </div>
  </div>
  <div class="bg-primary-lighter">
    <div class="container">
      <div class="row flex-row-reverse">
        <div class="col-md-6 break-vertical-block">
          <div class="bg-cover h-100 section-intro-image" style="background-image: url(https://images.unsplash.com/photo-1497052254059-f3e0bf1a5133?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=ce7267675921fc7e41b5610f49750a40&auto=format&fit=crop&w=685&q=80)"></div>
        </div>
        <div class="col-md-6 vertical-content d-flex justify-content-end">
          <div class="writing-md-vertical py-md-7 py-5 text-primary-light mr-auto">
            <p class="mb-0">青山依舊在，幾度夕陽紅。慣看秋月春風。</p>
            <p class="mb-0">一壺濁酒喜相逢，浪花淘盡英雄。是非成敗轉頭空，滾滾長江東逝水，白髮漁樵江渚上，古今多少事，都付笑談中。</p>
            <p class="mb-0">是非成敗轉頭空，滾滾長江東逝水，白髮漁樵江渚上，古今多少事，都付笑談中。</p>
          </div>
          <div class="break-vertical-block d-none d-md-block">
            <img src="/images/lg-為什麼一定要吃甜點.svg" width="88" alt="">
          </div>
        </div>
      </div>
    </div>
  </div>
</section>

<section class="text-center my-5 my-md-9">
  <img src="/images/lg-想吃甜點是不需要理由的.svg" class="img-fluid d-none d-md-inline-block" width="89" alt="">
  <img src="/images/sm-想吃甜點是不需要理由的.svg" class="img-fluid d-md-none" width="256" alt="">
</section>

<section class="container my-5 my-md-7">
    
  <div class="row">
    <div class="col-md-4" v-for="item in products" :key="item.id">
      <div class="item-card mb-4">
        <div class="item-image" :style="{ 'background-image': 'url(' + item.imageURL + ')' }">
          <div class="item-tag">{{item.category}}</div>
          <div class="item-stared-icon">
            <label class="ui-checked-display">
              <input type="checkbox" class="ui-checkbox">
              <i class="material-icons ui-show">
                favorite
              </i>
              <i class="material-icons ui-hidden">
                favorite_border
              </i>
            </label>
          </div>
        </div>
        <div class="item-body d-flex text-center">
          <div class="item-name col">{{item.title}}</div>
          <strong class="item-price col">NT$ {{item.price}}</strong>
        </div>
        <a href="#" class="btn-lg btn-primary-lighter btn btn-block" @click.prevent="addtoCart(item.id)">
            <i class="fas fa-spinner fa-spin"  v-if="item.id == status.loadingItem2" ></i>
          加入購物車
        </a>
      </div>
    </div>
    
  </div>


</section>
    </div>
</template>

<script>


import $ from 'jquery';

export default {
    props:["contentMsg"],
    data(){
        return {
            products:[],
            isNew:false,
            isLoading:false,

                        status:{
                loadingItem:'',
                loadingItem2:'',
            },
        };
    },
    methods: {
        getProducts(page = 1){
    const vm = this;
    const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`;
    console.log(process.env.APIPATH,process.env.CUSTOMPATH)
    this.$http.get(api).then(response =>{
        for(let i = 0; i < 3; i++) {
            vm.products.push(response.data.products[i]);
        }

    });
        },
        addtoCart(id, qty =1){
            const vm = this;
            const api =  `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            vm.status.loadingItem2=id;
            //開始頁面整理動畫
            this.$http.post(api, { "data": { "product_id" : id,"qty": qty } }).then(response =>{
            console.log(response.data);
            //關閉頁面整理動畫
            vm.status.loadingItem2='';
            this.$bus.$emit('cartQtyUpdate', 2);
            });
            },
    },

    created(){
        this.getProducts();
        
    },
}
</script>


